.project {
    opacity: 0;
    font-family: 'Oswald', sans-serif;
    background: linear-gradient(rgba(0,0,0,0.9), rgba(0,0,0,0.7));
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    height:100%;
    width: 100%;
    /* border-radius: 15px; */
    transition: 1s;
}

.project-container:hover .project {
    opacity: 1;
}

.company-image {
    justify-content: center;
    align-items: center;
    display: flex;
    padding-left: 30px;
    padding-right: 30px;
    height: 200px;
    overflow: hidden;
}
.company-image img {
    width: 100%;
}
